import React from 'react';
import styled from 'styled-components';

const Container = styled.div`
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin: 10px;
    justify-content: center;
    align-items: center;
    height: auto;
    width: 100%;
`;

const ThanksFor = styled.h3`
    width: 90%;
    flex-wrap: wrap;
    display: flex;
    gap: 10px;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
`;

const Title = styled.h2`
    font-size: 1.3rem;
    margin-bottom: 10px;
`;

const Name = styled.h3`
    display: flex;
    color: var(--gold);
`;

const SpecialThanksTo = ({ init }) => {
    return (
        <Container style={{opacity: !init && "0"}}>
            <Title>  </Title>
            <ThanksFor>
                  <Name></Name>
            </ThanksFor>
            <ThanksFor>
                 <Name></Name>
                </ThanksFor>
            <ThanksFor>
                  <Name> </Name>
            </ThanksFor>
            <ThanksFor>
                    <Name> </Name>
            </ThanksFor>
            <ThanksFor>
                SAYU PARTNER-S<Name></Name>
            </ThanksFor>
            <ThanksFor>
                     <Name></Name>
            </ThanksFor>
            <ThanksFor>
                  <Name>  </Name>
            </ThanksFor>
            <ThanksFor>  <Name></Name>
            </ThanksFor>
            <ThanksFor>
                <Name></Name>
            </ThanksFor>
            <ThanksFor>
                    <Name> </Name>
            </ThanksFor>
        </Container>
    );
};

export default SpecialThanksTo;